<template>
    <div class="live-sidebar-overlay" id="liveSidebarOverlay" ref="liveSidebarOverlay">
        <div class="live-sidebar" ref="liveSidebar">
            <sidebar-top></sidebar-top>

            <div class="bottom-sidebar flex flex-col justify-end">
                <div class="live-sidebar-nav">
                    <button class="live-sidebar-item mt-5" id="collapseBtn" @click="isToggled = !isToggled">
                        <div>
                            <span class="live-sidebar-icon">
                                <arrow-left-circle-icon></arrow-left-circle-icon>
                            </span>
                            <span class="ml-3 live-m-hide">Collapse</span>
                        </div>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import SidebarTop from '@/components/dashboard/SidebarTop';

import { ArrowLeftCircleIcon } from 'vue-feather-icons';

export default {
    components: {
        ArrowLeftCircleIcon,
        SidebarTop
    },
    data() {
        return {
            isToggled:false
        }
    },
    mounted() {
        this.$refs.liveSidebar.addEventListener('click', function(e) {
            e.stopPropagation();
        })

        this.closeSidebar();
    },
    watch: {
        isToggled(val){
            if(val){
                this.collapse()
                this.$store.commit('booking/mutsBar','close')
            }else{
                this.collapse()
                this.$store.commit('booking/mutsBar','open')
            }
        }
    },
    methods: {
        collapse() {
            let wrapper = document.getElementById('dashboardLayout');
            wrapper.classList.toggle('collapsed');
           
        },
        closeSidebar() {
            this.$refs.liveSidebarOverlay.addEventListener('click', function() {
                this.classList.remove('show');
                document.querySelector('html').style = null;
            })
            this.$store.commit('booking/mutsBar','open')
           
        }
    }
}
</script>

<style lang="postcss" scoped>
    .user-profile-sidebar {
        @apply flex-col items-center cursor-pointer;
    }

    .user-info {
        @apply pl-3 text-left flex-wrap mt-5;
    }
</style>