import { $axios } from '@/plugins/axios';

const state = {

    allcoach:[],
    coachID:'',
    sBar:null,
    timeSchedule:null
  
}

const mutations = {

    mutallcoach(state, allcoach){
        state.allcoach = allcoach
    },
    mutcoachID(state, coachID){
        state.coachID = coachID
    },
    mutsBar(state, sBar){
        state.sBar = sBar
    },
    muttimeSch(state, timeSchedule){
        state.timeSchedule = timeSchedule
    }

}

const actions = {

    async getallcoach({commit}) {
        const response = await $axios({
            method: "GET",
            url: '/coaches',
            headers: {
                'X-Requested-With': 'X-Requested-With'
            }
        })
        .then(res => {
            commit('mutallcoach', res.data);
            return res.data;
        }).catch(err => {
            console.log(err)
            return null
        });
        return response;
    },

    async getTimeSchedule({commit, state}, dateStr) {
        const response = await $axios({
            method: "GET",
            url: `/coaches/${state.coachID}?date=${dateStr}`,
            headers: {
                'X-Requested-With': 'X-Requested-With'
            }
        })
        .then(res => {
            commit('muttimeSch', res.data);
            return res.data;
        }).catch(err => {
            console.log(err)
            
            return null
        });
        return response;
    }

}

export default {
    namespaced:true,
    state,
    mutations,
    actions
}