const state = {
    user: null,
}

const mutations = {
    /**
     * Store user information to state
     *
     * @param {*} state
     * @param {*} user
     * @return {void}
     */
    authenticate(state, user) {
        state.user = user;
    },
    /**
     * Logout user
     *
     * @param {*} state
     * @return {void}
     */
    logout(state) {
        state.user = null;
    }
}

const actions = {
    async login() {

    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}