import Vue from 'vue';
import App from './App.vue';
import './registerServiceWorker';
import router from './router';
import store from './store';

// Plugin
import axiosPlugin from './plugins/axios';
import iconComponentPlugin from './plugins/icon';
import globalStatePlugin from './plugins/globalState';
import ErrorPlugin from './components/global/Error';

Vue.config.productionTip = false;

Vue.use(axiosPlugin);
Vue.use(iconComponentPlugin);
Vue.use(globalStatePlugin, store);
Vue.use(ErrorPlugin);

import './assets/css/app.css';

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app');
