const state = {
    all: {},
    message: '',
}

const mutations = {
    /**
     * mutate all state
     *
     * @param state
     * @param err
     * @return void
     */
    add(state, err) {
        // Laravel json exception format
        if(err.response.status && err.response.status == 422) {
            if(err.response.data && err.response.data.errors) {
                state.all = err.response.data.errors
            }
        }
    },
    /**
     * Remove current errors
     *
     * @param state
     * @return void
     */
    remove(state) {
        state.all = {},
        state.message = '';
    }
}

const getters = {
    $errors(state) {
        return {
            has: (field) => state.all[field] ? true : false,
            first: (field) => state.all[field][0] || '',
        }
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    getters
}