import Student from '@/views/student';
import Index from '@/views/student/index';
import StudentSettingRoute from './student/setting';

export default () => (
    {
        path: '/student',
        component: Student,
        children: [
            {
                path: '',
                component: Index
            },
            {
                name: 'book.coach',
                path: 'book',
                component: () => import('@/views/student/booking'),
            },
            {
                name: 'coachschedule',
                path: 'book/coach/:id',
                component: () => import('@/components/booking/ScheduleTime'),
            },
            {
                name: 'student.profile',
                path: 'profile',
                component: () => import('@/views/student/profile'),
            },

            StudentSettingRoute()
        ]
    }
)
